import React, { useEffect, useState } from 'react';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';


function Navbar(){

    const[movies, setMovies] = useEffect([])

    useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/poster/"
        }).then(response => {
            setMovies(response.data)
        })
    }, [])

  return (
    <div className="App">
        <div className="movie_bar">
                {movies.map(p =>(
                    <div className="item" key={p.id}>
                        <div className="item_title info">
                            <p>{movies.movie_name}</p>
                        </div>
                        <div className="item_year info">
                            <p>{movies.year}</p>
                        </div>
                        <div className="item_type info">
                            <p>{movies.get_type_display}</p>
                        </div>
                    </div>
                ))}
        </div>
    </div>
  );
}

export default Navbar;
